import { useState } from 'react';
import { Select, Spin } from 'antd';
import debounce from 'lodash/debounce';

import { getApiData } from '../server/utils.js';

const SearchUser = () => {
  const [search, setSearch] = useState(false);
  const [items, setItems] = useState([]);

  const searchUser = debounce(async (value: string) => {
    setSearch(true);
    setItems([]);
    const data = value
      ? await getApiData(null, `/search/users?q=${value}`)
      : {};
    const items = data.items || [];
    console.log(items);
    setSearch(false);
    setItems(items);
  }, 2000);

  return (
    <Select
      showSearch={true}
      notFoundContent={search ? <Spin size="small" /> : <span>no data</span>}
      filterOption={false}
      placeholder="搜索用户"
      allowClear={true}
      onSearch={searchUser}
      style={{ width: 150, marginBottom: 20 }}
    >
      {items.map((item: any) => (
        <Select.Option value={item.value} key={item.id}>
          {item.login}
        </Select.Option>
      ))}
    </Select>
  );
};

export default SearchUser;
